:root {
  --primary: #9D4C93;
}

.btn {
  padding: 8px, 20px;
  display: flex;
  border-radius: 4px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn:hover {
  padding: 8px, 20px;
  transition: all .3s ease-out;
  background-color: #ffffff;
  color: #4CB2DB;
  transition: 250ms;
}

.btn--primary {
  background-color: var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #ffffff;
  padding: 8px, 20px;
  border-radius: 4px;
  border: 1px solid var(--primary);
  transition: all .3s ease-out;
}

.btn--medium {
  padding: 8px, 20px;
  border-radius: 4px;
  font-size: 18px;
  color: #ffffff;
}

.btn--large {
  padding: 12px, 26px;
  border-radius: 4px;
  font-size: 30px;
  color: #ffffff;
}

@media screen and (max-width: 768px) {
  .btn--large {
    padding: 6px, 10px;
    border-radius: 2px;
    font-size: 18px;
    color: #ffffff;
  }
}