.App {
  text-align: center;
  background-color:  #001F47;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-top: 1em;
  padding-bottom: 3em;
}

#pdf-section {
  border: 5px solid #4CB2DB;
  background-color: #4C4C93;
  box-shadow: -43px 37px 0px #884C93;
}

.mobile-btn {
  display: none;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s linear;
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* ======= */

#loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #4C6893;
  opacity: 90%;
}

@media screen and (max-width: 768px) {
  .pdf-container {
    display: flex;

  }

  .mobile-btn {
    display: flex;
  }

  .mobile-inactive {
    display: none;
  }

  .App {
    text-align: center;
    background-color:  #001F47;
    min-height: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 2vmin);
    color: white;
    padding-top: 4em;
    padding-bottom: 0em;
  }

}