.homepage-main {
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  background: #001F47;

}

.homepage-container {
  padding: 3em 3em;
  background: #001F47;
  display: flex;
  flex-direction: row;
}

.image-container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 1rem 1rem;
}

.homepage-image {
  max-height: 50vh;
  max-width: 259px;
  border: 2px solid white;
  border-radius: 15%;
  box-shadow: -8px 7px 0px #9D4C93;
  display: flex;
}

.feature-container {
  background: #001F47;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: calc(20px + 2vw);
}


.feature-container div{
  background: #001F47;
}

.homepage-quote {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

.quote-sub {
  color: turquoise;
  font-size: calc(10px + 2vw);

}

#quote-span {
  background: #001F47;
  color: #9D4C93;

}

.btn-container {
  display: flex;
}


@media screen and (max-width: 768px) {
  .homepage-container {
    padding: 3em 3em;
    background: #001F47;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 2vw);
  }

  .homepage-image {
    max-height: 40vh;
    max-width: 230px;
    border: 2px solid white;
    border-radius: 15%;
    box-shadow: -8px 7px 0px #9D4C93;
    display: flex;
  }
}